/* eslint-disable no-mixed-spaces-and-tabs */
import React, { useState, useEffect } from "react"
import { Layout, Heading, Empty, Button, Avatar, Section } from "components"
import { Row, Col, List, Tag, Skeleton } from "antd"
import { Link, useHistory } from "react-router-dom"
import moment from "moment"
import styled from "styled-components"
import { useQuery } from "react-apollo"
import { queryListPostingAssignment } from "queries/profile"
import { newClient, media, mobile } from "helpers"
import { baseStyles } from "styles/base"
import { Trans, t } from "@lingui/macro"
import { country } from "App"
import { withI18n } from "@lingui/react"

const ListTitle = styled.div`
	.intro-title {
		.datetime {
			color: ${baseStyles.greyColor};
		}
	}
`

const StyledSection = styled(Section)`
	margin-bottom: 0;
	${media.mobile`
		padding-left: 0;
	`}
`

const ListItem = styled(List.Item)`
	&& {
		padding: 2em;
		background-color: #fff;
		border-radius: 10px;
		box-shadow: ${baseStyles.boxShadow.main};
		cursor: pointer;
		transition: all 0.2s ease;
		margin-bottom: 1.5em;
		&:hover {
			box-shadow: ${baseStyles.boxShadow.hover};
			transform: translateY(-3px);
		}
	}
`

const theClient = newClient("posting_assignment")

function Assignments({ i18n }) {
	const [assignmentList, setAssignmentList] = useState([])
	const { push } = useHistory()

	const { loading } = useQuery(queryListPostingAssignment, {
		client: theClient,
		onCompleted: (data) => {
			data = data.listPostingAssignment || []
			setAssignmentList(data)
		},
	})

	const renderAssignments = (loading) => (
		<List
			itemLayout="horizontal"
			dataSource={assignmentList}
			// loading={loading}
			locale={{
				emptyText: !loading ? <Empty description={i18n._(t`No assignment listings found`)} /> : null,
			}}
			renderItem={({ profileJoin = [], ...item }) => {
				const statusColor = item.isActive === 1 ? "#87d068" : "#999"
				const title = item.title.replace(/\s/g, "-")
				return (
					<ListItem onClick={() => push(`/${country}/assignments/${title}--${item.pvid}`)}>
						<Skeleton active loading={loading} paragraph={{ rows: 1 }}>
							<List.Item.Meta
								avatar={<Avatar src="" size="large" />}
								title={
									<ListTitle>
										<p className="intro-title">
											<Trans>{item.title}</Trans> {mobile ? <br /> : null}
											<span className="datetime">{moment(item.createdAt).fromNow()}</span> &nbsp;{" "}
											<Tag color={statusColor}>
												{item.isActive === 1 ? <Trans>Active</Trans> : <Trans>Inactive</Trans>}
											</Tag>
										</p>
									</ListTitle>
								}
								description={
									<StyledSection noPadding>
										<Row gutter={32}>
											<Col lg={8}>
												<Heading
													reverse
													content={<Trans>Subject</Trans>}
													subheader={item.programmeSubjectName}
													marginBottom="0"
												/>
											</Col>
											<Col lg={8}>
												<Heading
													reverse
													content={<Trans>Curriculum</Trans>}
													subheader={item.programmeLevelName}
													marginBottom="0"
												/>
											</Col>
											<Col lg={8}>
												<Heading
													reverse
													content={<Trans>Preferred tutor experience</Trans>}
													subheader={<Trans>{item.tutorExperience} years</Trans>}
													marginBottom="0"
												/>
											</Col>
										</Row>
									</StyledSection>
								}
							/>
						</Skeleton>
					</ListItem>
				)
			}}
		/>
	)

	useEffect(() => {
		if (!localStorage.getItem("token")) {
			push(`/${country}/login`)
		}
	}, [push])

	return (
		<Layout>
			<Section paddingHorizontal={0}>
				<Row type="flex" justify="center">
					<Col lg={12}>
						<Row type="flex" justify="space-between" align="middle" style={{ marginBottom: "2em" }}>
							<Col lg={12}>
								<Heading
									bold
									content={<Trans>Your assignments</Trans>}
									subheader={<Trans>The list of your posted assignments</Trans>}
									level={4}
								/>
							</Col>
							<Col lg={8} style={{ textAlign: "right" }}>
								<Link to={`/${country}/assignments/post_assignment`}>
									<Button type="primary" size="medium" icon="plus">
										&nbsp; <Trans>Post new assignment</Trans>
									</Button>
								</Link>
							</Col>
						</Row>
						{renderAssignments(loading)}
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}

export default withI18n()(Assignments)
